import React, { useState } from "https://cdn.skypack.dev/react@17.0.1?dts";
import "./Join.css";
import { Logo } from "../components/Logo.tsx";
import { Field } from "../components/Field.tsx";
import { Button } from "../components/Button.tsx";
import { ButtonContainer } from "../components/ButtonContainer.tsx";

interface JoinScreenProps {}

export const JoinScreen = (props: JoinScreenProps) => {
  const [userName, setUserName] = useState<string | null>(null);
  const [roomName, setRoomName] = useState<string | null>(null);

  return (
    <section className="Screen JoinScreen">
      <Logo />
      <form
        className="JoinScreen_Form"
        onSubmit={(e) => {
          e.preventDefault();
        }}
      >
        <Field label="Name" name="userName" onChange={setUserName} />
        <Field
          label="Room Name"
          name="roomName"
          onChange={setRoomName}
          paddingTop="s4"
        />
        <ButtonContainer paddingTop="s7">
          <Button appearance="primary">Join</Button>
          <Button appearance="secondary">Create</Button>
        </ButtonContainer>
      </form>
    </section>
  );
};
