import React, { useState } from "https://cdn.skypack.dev/react@17.0.1?dts";
import { render } from "https://cdn.skypack.dev/react-dom@17.0.1?dts";

import { getClient } from "./api/moviematch.ts";
import { usePromise } from "./hooks/usePromise.ts";

import "./main.css";

import { Spinner } from "./components/Spinner.tsx";

import { LoginScreen } from "./screens/Login.tsx";
import { JoinScreen } from "./screens/Join.tsx";
import { CreateScreen } from "./screens/Create.tsx";
import { RateScreen } from "./screens/Rate.tsx";

const client = getClient();

const MovieMatch = () => {
  const [screen, setScreen] = useState<"login" | "join" | "create" | "rate">(
    "login"
  );
  const [clientConnected, config, error] = usePromise(
    client.waitForMessage("config"),
    [client]
  );

  console.log({ clientConnected, config, error });

  if (!clientConnected || !config) {
    return (
      <section className="Screen">
        <Spinner />
      </section>
    );
  }

  switch (screen) {
    case "login":
      return <LoginScreen config={config.payload} />;
    case "join":
      return <JoinScreen />;
    case "create":
      return <CreateScreen />;
    case "rate":
      return <RateScreen />;
  }
};

render(<MovieMatch />, document.getElementById("app"));

if (
  window.innerHeight !==
  document.querySelector("body")?.getBoundingClientRect().height
) {
  document.body.style.setProperty("--vh", window.innerHeight / 100 + "px");
  window.addEventListener("resize", () => {
    document.body.style.setProperty("--vh", window.innerHeight / 100 + "px");
  });
}
