import React, { useState } from "https://cdn.skypack.dev/react@17.0.1?dts";
import "./Login.css";
import { Logo } from "../components/Logo.tsx";
import { Field } from "../components/Field.tsx";
import { Button } from "../components/Button.tsx";
import { ButtonContainer } from "../components/ButtonContainer.tsx";
import { getToken, signIn } from "../api/plex.tv.ts";
import { Config } from "../../../../types/moviematch.d.ts";

interface LoginScreenProps {
  config: Config;
}

export const LoginScreen = ({ config }: LoginScreenProps) => {
  const [userName, setUserName] = useState<string | null>(null);

  return (
    <section className="Screen LoginScreen">
      <Logo />
      <form
        className="LoginScreen_Form"
        onSubmit={(e) => {
          e.preventDefault();
        }}
      >
        <Field label="Name" name="userName" onChange={setUserName} />

        <ButtonContainer paddingTop="s7">
          {!config.requirePlexLogin && (
            <Button appearance="primary">Sign In</Button>
          )}
          <Button
            appearance="primary"
            color="plex-color"
            onPress={() => {
              getToken().then((token) => {
                if (token) {
                  console.log(token);
                } else {
                  return signIn();
                }
              });
            }}
          >
            Sign In with Plex
          </Button>
        </ButtonContainer>
      </form>
    </section>
  );
};
